import { atom } from "recoil";

export interface IToDo {
  id: number;
  text: string;
}

/* [key: string] = key  3      */
interface ItoDoState {
  [key: string]: IToDo[];
}

export const toDoState = atom<ItoDoState>({
  key: "toDos",
  default: JSON.parse(localStorage.getItem("toDos") as string) || {
    "To Do": [],
    Doing: [],
    Done: [],
  },
});
